*{margin: 0; padding: 0; box-sizing: border-box;}
header{
  background-color: crimson;
  color: white;
  height: 70px;
}
header h1{
  font-size: 30px;
  line-height: 70px;
  margin-left: 1.5rem;
  font-weight: bold;
  /* padding: 1rem auto; */
}
.box{
  padding: 1.5rem;
  border-bottom: 1px solid black;
}
.box h3{
  display: inline-block;
  margin-right: 1rem;
}
.box span{
  cursor: pointer;
  display: inline-block;
}
.modal{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  z-index: 10;
  border: 1px solid black;
  padding: 2rem;
  /* display: none; */
}